.address__line {
    width: 50%;
    margin: 20px auto;
    background-color: #12C93D;
    height: 3px;
}

.address__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 50px;
}

.address__item {
    display: grid;
    row-gap: 20px;
    border-left: 3px solid #12C93D;
}

.address__item>h3 {
    font-weight: bold;
    font-size: 24px;

}

.address__item>p {
    font-size: 20px;
}

.address__item>* {
    margin-left: 15px;
}

.address__icon {
    width: 50px;
    color: #000;
}

@media (max-width: 500px) {
    .address__wrapper {
        grid-template-columns: 1fr;

    }

    .address__item {
        border-bottom: 3px solid #fff;

    }

    .address__item>* {
        margin-left: 70px;
    }
}